* {
  margin: 0%;
  padding: 0%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  font-family: sans-serif;
}

@media (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}
@media (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}
@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

body {
  font-weight: 400;
  line-height: 1.6;
  min-height: 100vh;
  display: inline;
}

.progress {
  width: 4rem;
  font-size: 4rem;
  color: lightgray;
  margin-top: 2rem;
}

.bar {
  color: rgb(0, 128, 38) !important;
}

.form {
  display: flex;
  flex-direction: column;

  align-content: center;
  align-items: center;
}

.form2 {
  display: flex;
  flex-direction: column;

  align-content: center;
  align-items: center;
}

.Error {
  font-size: 2.5rem;
  margin-top: 2rem;
  background-color: red;
  color: white;
  text-transform: uppercase;
  border-radius: 2rem;
  padding: 0.3rem;
}

.Listo {
  font-size: 2.5rem;
  margin-top: 2rem;
  background-color: green;
  color: white;
  text-transform: uppercase;
  border-radius: 2rem;
  padding: 0.3rem;
  text-align: center;
}

.warning {
  font-size: 2.5rem !important;
  margin-top: 2rem;
  background-color: rgba(216, 156, 27, 0.678);
  color: white;
  text-transform: uppercase;

  padding: 0.3rem;
  text-align: center;
}

.container {
  width: 180vh;

  margin: 0 auto;
  display: flex;
  align-content: center;
  align-items: center;
  flex-direction: column;
  background-image: linear-gradient(rgb(245, 245, 245));
}

.logo-img {
  width: 30rem;
  height: auto;

  border: 2px;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  animation: animateup 3s;
}

.main-heading {
  font-size: 3.3rem;
  color: rgb(46, 45, 45);
  font-weight: 200;
  text-transform: uppercase;
  display: flex;
  letter-spacing: -0.01rem;
  animation: apear 1s 1.5s backwards;
  font-family: Georgia, "Times New Roman", Times, serif;
}

p {
  animation: apear 1s 1.5s backwards;
  font-size: 2rem;
  letter-spacing: 0.01rem;
  text-transform: uppercase;
  color: lightslategray;
}

.search {
  width: 50vh;
  background-color: whitesmoke;
  box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
  font-size: 2.5rem;
  text-align: center;
  height: 4.5rem;
  outline: none;
  border: solid;
  border-color: rgba(77, 76, 76, 0.397);
  border-radius: 1.2rem;
  margin-bottom: 2rem;
  transition: all 0.6s;
  animation: apear 1s 1.5s backwards;
}

.search:focus {
  outline: none;
}

input::placeholder {
  text-align: center;
  font-size: 1.75rem;
  color: lightslategray;
}

.search:hover {
  width: 90vh;
}

.submit {
  height: 6rem;
  width: 20rem;
  border: none;
  border-radius: 5rem;
  background-color: rgb(17, 65, 107);
  font-size: 2.5rem;
  color: white;
  outline: none;

  transition: all 0.1s;
  box-shadow: 0rem 0.2rem #888888;
  direction: none;
  animation: apear 1s 1.5s backwards;
}

.submit:active {
  transform: translateY(0.5rem);
  direction: none;
}

.submit:focus {
  outline: none;
}

.information {
  display: flex;
  justify-content: space-between;
  margin-right: auto;
  flex: 1;

  margin-top: 2rem;
  animation: apear 0.5s 1s backwards;
  transition: all 0.5s;
}

.container2 {
  width: 100vh;
  margin: 0 auto;
}

.basic-info {
  align-items: center;
}

.Nombre {
  width: 100%;
}

h3 {
  font-size: 3rem;
  margin-bottom: 1rem;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  letter-spacing: 0.3rem;
}

span {
  font-size: 3.5rem;
  font-weight: 100;

  border-radius: 5px;
  box-sizing: border-box;
}

h2 {
  font-size: 3.5rem;
}

p {
  font-size: 2rem;
}
.separator {
  width: 100%;
  height: 5rem;
  border-bottom: 1px dotted grey;
}
.nombre-info {
  margin-bottom: 3rem;
}
@keyframes animateup {
  0% {
    transform: translateY(-120rem);
  }

  50% {
    transform: translateY(0);
  }
}

@keyframes apear {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}

.navigation__checkbox {
  display: none;
}

.navigation__button {
  background-color: white;
  height: 7rem;
  width: 7rem;
  position: fixed;
  top: 6rem;
  right: 6rem;
  border-radius: 50%;
  z-index: 2000;
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
}
@media (max-width: 56.25em) {
  .navigation__button {
    top: 4.5rem;
    right: 4.5rem;
  }
}
@media (max-width: 37.5em) {
  .navigation__button {
    top: 3.5rem;
    right: 3.5rem;
  }

  .Listo {
    font-size: 2rem;
    width: 80%;
  }

  .warning {
    font-size: 2rem;
    width: 80%;
  }
  .progress {
    width: 8rem;
    font-size: 8rem;
    color: lightgray;
    margin-top: 2rem;
  }

  .navigation__nav {
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1500;
    opacity: 0;
    width: 0;
    transition: all 0.8s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  }

  .navigation__list {
    position: absolute;
    top: 0%;
    left: 50%;
    transform: translate(-50%, 50%);
    list-style: none;
    text-align: center;
    width: 100%;
  }

  .navigation__item {
    margin: 1rem;
  }

  .navigation__link:link,
  .navigation__link:visited {
    display: inline-block;
    font-size: 3rem;
    font-weight: 300;
    color: white;
    padding: 1rem 2rem;
    text-decoration: none;
    text-transform: uppercase;
    background-image: linear-gradient(
      120deg,
      transparent 0%,
      transparent 50%,
      white 50%
    );
    background-size: 300%;
    transition: all 0.4s;
  }

  .navigation__link:hover,
  .navigation__link:active {
    background-position: 100%;
    color: #0a0a0a;
    transform: translateX(1rem);
  }

  .navigation__checkbox:checked ~ .navbackground {
    transform: scale(80);
  }

  .navigation__checkbox:checked ~ .navigation__nav {
    opacity: 1;
    width: 100%;
  }

  .navigation__icon {
    position: relative;
    margin-top: 3.5rem;
  }
  .navigation__icon,
  .navigation__icon::before,
  .navigation__icon::after {
    width: 3rem;
    height: 2px;
    background-color: black;
    display: inline-block;
  }
  .navigation__icon::before,
  .navigation__icon::after {
    content: "";
    position: absolute;
    left: 0;
    transition: all 0.2s;
  }
  .navigation__icon::before {
    top: -0.8rem;
  }
  .navigation__icon::after {
    top: 0.8rem;
  }

  .navigation__button:hover .navigation__icon::before {
    top: -1rem;
  }

  .navigation__button:hover .navigation__icon::after {
    top: 1rem;
  }

  .navigation__checkbox:checked + .navigation__button .navigation__icon {
    background-color: transparent;
  }

  .navigation__checkbox:checked
    + .navigation__button
    .navigation__icon::before {
    top: 0;
    transform: rotate(45deg);
  }

  .navigation__checkbox:checked + .navigation__button .navigation__icon::after {
    top: 0;
    transform: rotate(-45deg);
  }

  .navbackground {
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    position: fixed;
    top: 6.5rem;
    right: 6.5rem;
    background-image: radial-gradient(#183583, #115874);
    z-index: 1000;
    transition: transform 0.8s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  }
  @media (max-width: 56.25em) {
    .navbackground {
      top: 4.5rem;
      right: 4.5rem;
    }
  }
  @media (max-width: 37.5em) {
    .navbackground {
      top: 3.5rem;
      right: 3.5rem;
    }

    .container2 {
      width: 85vh;
      margin-left: 2rem;
    }

    .left {
      margin-left: 2rem;
    }
  }

  .bar {
    color: rgb(0, 128, 38) !important;
  }

  @media (max-width: 75em) {
    .container {
      width: 100vw;
    }
  }
  @media (max-width: 56.25em) {
    .container {
      width: 100vw;
    }
  }
  @media (min-width: 112.5em) {
    .container {
      width: 280vh;
    }
  }

  @media (max-width: 75em) {
    .container2 {
      width: 90vh;
    }
  }
  @media (max-width: 56.25em) {
    .container2 {
      width: 60vh;
    }
  }
  @media (min-width: 112.5em) {
    .container2 {
      width: 140vh;
    }
  }

  @media (max-width: 75em) {
    .main-heading {
      font-size: 2.5rem;
    }
  }
  @media (max-width: 56.25em) {
    .main-heading {
      font-size: 2.3rem;
      text-align: center;
    }
  }
  @media (min-width: 112.5em) {
    .main-heading {
      font-size: 4rem;
    }
  }

  @media (max-width: 75em) {
    .logo-img {
      width: 30rem;
      height: auto;
    }
  }
  @media (max-width: 56.25em) {
    .logo-img {
      width: 20rem;
      height: auto;
    }
  }
  @media (min-width: 112.5em) {
    .main-heading {
      font-size: 4rem;
    }
  }

  @media (max-width: 56.25em) {
    .search {
      width: 50vw;
      font-size: 2.5rem;
      text-align: center;
      height: 4.5rem;
      border: none;
      border-radius: 2rem;
      margin-bottom: 2rem;
      transition: all 0.6s;
      animation: apear 1s 1.5s backwards;
    }

    .search:focus {
      outline: none;
    }

    input::placeholder {
      text-align: center;
      font-size: 1.75rem;
      color: lightslategray;
    }

    .search:hover {
      width: 65vw;
    }

    span {
      font-size: 2rem;
    }

    h3 {
      font-size: 2rem;
    }

    h2 {
      font-size: 2rem;
    }
  }

  @media (max-width: 37.5em) {
    p {
      font-size: 1.5rem;
    }
    .main-heading {
      font-size: 2.5rem;
      letter-spacing: none;
    }

    .information {
      flex-direction: column;
    }
    .search {
      width: 50vw;
      font-size: 2.5rem;
      text-align: center;
      height: 4.5rem;
      border: none;
      border-radius: 2rem;
      margin-bottom: 2rem;
      transition: all 0.6s;
      animation: apear 1s 1.5s backwards;
    }

    .search:focus {
      outline: none;
    }

    input::placeholder {
      text-align: center;
      font-size: 1.75rem;
      color: lightslategray;
    }

    .search:hover {
      width: 50vw;
    }

    span {
      font-size: 3.5rem;
    }

    h3 {
      font-size: 2rem;
    }

    h2 {
      font-size: 1.9rem;
    }
  }
}
